.Background {
  height: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  position: relative;
}

.Background h1 {
  font-family: "Bebas Neue", sans-serif;
  font-size: 5rem;
  position: absolute;
  color: white;
  text-align: center;
  width: 50%;
  top: 18%;
  left: 25%;
}

@media (max-width: 1410px) {
  .Background {
    height: 650px;
  }
}

@media (max-width: 1200px) {
  .Background {
    height: 550px;
  }

  .Background h1 {
    top: 30%;
  }
}

@media (max-width: 1000px) {
  .Background {
    height: 450px;
  }

  .Background h1 {
    font-size: 2.5rem;
  }
}

@media (max-width: 900px) {
  .Background {
    height: 350px;
  }

  .Background h1 {
    top: 20%;
    font-size: 2rem;
  }
}

@media (max-width: 600px) {
  .Background {
    height: 250px;
  }

  .Background h1 {
    font-size: 1.5rem;
  }
}
